.container.testimonials__container {
	width: 40%;
	padding-bottom: 4rem;
}

.testimonials__container {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 3rem;
}
.client__avatar {
	width: 4rem;
	aspect-ratio: 1/1;
	overflow: hidden;
	border-radius: 50%;
	margin: 0 auto 1rem;
	border: 2px solid var(--color--primary);
}

.testimonial {
	background: var(--color-bg-variant);
	text-align: center;
	padding: 2rem;
	border-radius: 2rem;
	user-select: none;
}

.client__review {
	color: var(--color-light);
	font-weight: 300;
	display: block;
	width: 80%;
	margin: 0.8rem auto 0;
}

/* ==================== MEDIA QUERIES (MEDIUM) TABLET SCREENS ================ */
@media screen and (max-width: 10224px) {
	.container.testimonials__container {
		width: 60%;
	}

	.testimonials__container {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		gap: 2rem;
	}
}

/* ==================== MEDIA QUERIES (SMALL) MOBILE SCREENS ================ */
@media screen and (max-width: 600px) {
	.container.testimonials__container {
		width: var(--container-width-sm);
	}

	.testimonials__container {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		gap: 1rem;
	}

	.client__review {
		width: var(--container-width-sm);
	}
}