@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
header {
	height: 80vh;
	padding-top: 7rem;
	overflow: hidden;
}

.header__container {
	text-align: center;
	height: 100%;
	position: relative;
}

/* ===================== CTA ===================== */

.cta {
	margin-top: 2.5rem;
	display: flex;
	grid-gap: 1.2rem;
	gap: 1.2rem;
	justify-content: center;
}

/* ===================== HEADER SOCIALS ===================== */

.header__socials {
	display: flex;
	flex-direction: column;
	align-items: center;
	grid-gap: 0.8rem;
	gap: 0.8rem;
	position: absolute;
	bottom: 3rem;
	left: 0;
}

.header__socials::after {
	content: "";
	width: 0.4px;
	height: 2rem;
	background: var(--color--primary);
}

/* ===================== LOGO ===================== */
.logo {
	height: 120px;
	width: 120px;
	position: absolute;
	left: 0rem;
	top: 0rem;
	margin-left: 0%;
	margin-top: 5%;
	margin-bottom: 0%;
	margin-right: 0%;
	/* background-color: grey; */
	/* border-radius: 70px; */
	/* box-shadow: 0px 0px 22px rgba(0, 0, 0, 0.3);	 */
	/* border-radius: 12rem 12rem 0 0; */
	/* overflow: hidden; */
	padding: 0rem 0rem 0rem 0rem;
}

/* ===================== ME ===================== */
.me {
	background: linear-gradient(var(--color--primary), transparent);
	width: 22rem;
	height: 30rem;
	position: absolute;
	left: calc(50% - 11rem);
	margin-top: 4rem;
	border-radius: 12rem 12rem 0 0;
	overflow: hidden;
	padding: 5rem 1.5rem 1.5rem 1.5rem;
}

h1 {
	font-size: 10%;
	line-height: 10px;
	vertical-align: baseline;
	letter-spacing: normal;
	word-spacing: 0px;
}

/* ===================== SCROLL DOWN ===================== */

.scroll__down {
	position: absolute;
	right: -2.3rem;
	bottom: 5rem;
	-webkit-transform: rotate(90deg);
	        transform: rotate(90deg);
	font-weight: 100;
	font-size: 0.9rem;
	/* box-shadow: 0px 0px 22px rgba(0, 0, 0, 0.3);	 */
}

/* ===================== MEDIA QUERIES (MEDIUM) TABLET SCREENS ===================== */
@media screen and (max-width: 1024px) {
	header {
		height: 80vh;
	}

	.logo {
		/* position: static; */
		margin-top: 3rem;
		margin-left: 0%;
		/* height: 50%; */
		position: absolute;
		width: 15%;
		margin-left: -2rem;
	}

}

/* ===================== MEDIA QUERIES (SMALL) MOBILE SCREENS ===================== */
@media screen and (max-width: 600px) {
	header {
		height: 100vh;
	}

	.scroll__down, .header__socials {
		bottom: 1rem;
	}

	.logo {
		/* position: static; */
		margin-top: -6rem;
		margin-left: 40%;
		/* height: 50%; */
		position: relative;
		width: 20%;
	}
}
nav {
	background: rgba(0, 0, 0, 0.3);
	width: -webkit-max-content;
	width: max-content;
	display: block;
	padding: 0.7rem 1.7rem;
	z-index: 2;
	position: fixed;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	bottom: 2rem;
	display: flex;
	grid-gap: 0.8rem;
	gap: 0.8rem;
	border-radius: 3rem;
	-webkit-backdrop-filter: blur(15px);
	        backdrop-filter: blur(15px);
}

nav a {
	background: transparent;
	padding: 0.9rem;
	border-radius: 50%;
	display: flex;
	color: var(--color-light);
	font-size: 1.1rem;
}

nav a:hover {
	background: rgba(0, 0, 0, 0.3);
}

nav a.active {
	background: var(--color-bg);
	color: var(--color-white);
}

