.about {
	display: grid;
	grid-template-columns: 35% 50%;
	grid-gap: 15%;
	gap: 15%;
}

.about__me {
	width: 100%;
	aspect-ratio: 1/1;
	border-radius: 2rem;
	background: linear-gradient(
		45deg,
		grey,
		transparent
	);
	display: grid;
	place-items: center;
}

.about__me-image {
	border-radius: 2rem;
	overflow: hidden;
	-webkit-transform: rotate(10deg);
	        transform: rotate(10deg);
	transition: var(--transition);
}

.about__me-image:hover {
	-webkit-transform: rotate(0);
	        transform: rotate(0);
}

.about__cards {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 1.5rem;
	gap: 1.5rem;
}

.about__card {
	background: var(--color--primary);
	border: 1px solid transparent;
	border-radius: 1rem;
	text-align: center;
	transition: var(--transition);
}

.about__card:hover {
	background: var(--color-bg-variant);
	border-color: var(--color--primary);
	cursor: default;
}

.about__icon {
	color: var(--color-white);
	font-size: 1.4rem;
	margin-top: 1rem;
	margin-bottom: 1rem;
}

.about__card h5 {
	font-size: 0.95rem;
}

.about__card small {
	font-size: 0.7rem;
	color: var(--color-white);
}

.about__content p {
	margin: 2rem 0 2.6rem;
	color: var(--color-light);
}

/* ==================== MEDIA QUERIES (MEDIUM) TABLET SCREENS ================ */
@media screen and (max-width: 10224px) {
	.about__container {
		grid-template-columns: 1fr;
		grid-gap: 0;
		gap: 0;
	}

	.about__me {
		width: 50%;
		margin: 2rem auto 4rem;
	}

	.about__content p {
		margin: 1rem 0 1.5rem;
	}
}

/* ==================== MEDIA QUERIES (SMALL) MOBILE SCREENS ================ */
@media screen and (max-width: 600px) {
	
	.about__me {
		width: 65%;
		margin: 0rem auto 3rem;
	}
	
	.about__cards {
		grid-template-columns: 1fr 1fr;
		grid-gap: 1rem;
		gap: 1rem;
	}

	.about__content {
		text-align: center;
	}

	.about__content p {
		margin: 1.5rem 0;
	}
}
.experience__container{
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 2rem;
	gap: 2rem;
}

.experience__container > div {
	background: var(--color-bg-variant);
	padding: 2.4rem 5rem;
	border-radius: 1.5rem;
	border: 0.5px solid transparent;
	transition: var(--transition);
}

.experience__container > div:hover {
	background: transparent;
	border-color: var(--color--primary);
	cursor: default;
}

.experience__container > div h3{
	text-align: center;
	margin-bottom: 2rem;
	color: var(--color--primary);
}

.experience__content {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-row-gap: 2rem;
	row-gap: 2rem;
	grid-column-gap: 4.5rem;
	-webkit-column-gap: 4.5rem;
	        column-gap: 4.5rem;
}

.experience__details {
	/* display: flex; */
	grid-gap: 1rem;
	gap: 1rem;
}

.experience__details-icon {
	margin-top: 6px;
	color: var(--color--primary);
}

/* ==================== MEDIA QUERIES (MEDIUM) TABLET SCREENS ================ */
@media screen and (max-width: 10224px) {
	.experience__container {
		grid-template-columns: 1fr;
	}

	.experience__container > div {
		width: 80%;
		padding: 2rem;
		margin: 0 auto;
	}

	.experience__content {
		padding: 1rem;
	}
}

/* ==================== MEDIA QUERIES (SMALL) MOBILE SCREENS ================ */
@media screen and (max-width: 600px) {
	.experience__container > div {
		width: 100%;
		padding: 2rem 1rem;
		/* margin: 0 auto; */
	}

	.experience__content {
		grid-gap: 1rem;
		gap: 1rem;
	}

}
.services__container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 3rem;
	gap: 3rem;
}

.service {
	background: var(--color-bg-variant);
	border-radius: 1rem 1rem 1rem 1rem;
	border: 0.5px solid transparent;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	transition: var(--transition);
}

.services__container > div:hover {
	background: transparent;
	border-color: var(--color--primary);
	cursor: default;
}

.services__container > div h3{
	text-align: center;
	margin-bottom: 2rem;
	color: var(--color--primary);
}

.service:hover {
	background: transparent;
	border-color: var(--color--primary);
	cursor: default;
}

.service__head {
	background: var(--color--primary);
	padding: 2rem;
	border-radius: 1rem 1rem 0rem 0rem;
	box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.service__head h3 {
	color: var(--color-bg);
	font-size: 1rem;
	text-align: center;
}

.service__list {
	padding: 2rem;
}

.service__list li {
	display: flex;
	grid-gap: 1rem;
	gap: 1rem;
	margin-bottom: 0.8rem;
}

.service__list-icon {
	color: var(--color--primary);
	margin-top: 2px;
	width: 20%;
}

.service__list p {
	font-size: 0.8rem;
}

/* ==================== MEDIA QUERIES (MEDIUM) TABLET SCREENS ================ */
@media screen and (max-width: 10224px) {
	.services__container {
		grid-template-columns: 1fr 1fr;
		grid-gap: 2rem;
		gap: 2rem;
	}

	.service {
		height: auto;
	}
}

/* ==================== MEDIA QUERIES (SMALL) MOBILE SCREENS ================ */
@media screen and (max-width: 600px) {
	.services__container {
		grid-template-columns: 1fr;
		grid-gap: 1.5rem;
		gap: 1.5rem;
	}
}
.portfolio__container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 2.5rem;
	gap: 2.5rem;
}

.portfolio__item {
	/* background: var(--color-bg-variant); */
	padding: 1.3rem;
	border-radius: 0.2rem;
	border: 1px solid transparent;
	transition: var(--transition);
}

.portfolio__item:hover {
	background: var(--color-bg-variant);
	border-color: var(--color--primary);
	/* background: transparent; */
}

.portfolio__item-image {
	border-radius: 1.5rem;
	overflow: hidden;
}

.portfolio__item h3 {
	margin: 1.2rem 0 2rem;
}

.portfolio__item-cta {
	display: flex;
	grid-gap: 1rem;
	gap: 1rem;
	margin-bottom: 1rem;
}
/* ==================== MEDIA QUERIES (MEDIUM) TABLET SCREENS ================ */
@media screen and (max-width: 10224px) {
	.portfolio__container {
		grid-template-columns: 1fr 1fr;
		grid-gap: 1.2rem;
		gap: 1.2rem;
	}
}

/* ==================== MEDIA QUERIES (SMALL) MOBILE SCREENS ================ */
@media screen and (max-width: 600px) {
	.portfolio__container {
		grid-template-columns: 1fr;
		grid-gap: 1rem;
		gap: 1rem;
	}
}
.container.testimonials__container {
	width: 40%;
	padding-bottom: 4rem;
}

.testimonials__container {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 3rem;
	gap: 3rem;
}
.client__avatar {
	width: 4rem;
	aspect-ratio: 1/1;
	overflow: hidden;
	border-radius: 50%;
	margin: 0 auto 1rem;
	border: 2px solid var(--color--primary);
}

.testimonial {
	background: var(--color-bg-variant);
	text-align: center;
	padding: 2rem;
	border-radius: 2rem;
	-webkit-user-select: none;
	        user-select: none;
}

.client__review {
	color: var(--color-light);
	font-weight: 300;
	display: block;
	width: 80%;
	margin: 0.8rem auto 0;
}

/* ==================== MEDIA QUERIES (MEDIUM) TABLET SCREENS ================ */
@media screen and (max-width: 10224px) {
	.container.testimonials__container {
		width: 60%;
	}

	.testimonials__container {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		grid-gap: 2rem;
		gap: 2rem;
	}
}

/* ==================== MEDIA QUERIES (SMALL) MOBILE SCREENS ================ */
@media screen and (max-width: 600px) {
	.container.testimonials__container {
		width: var(--container-width-sm);
	}

	.testimonials__container {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		grid-gap: 1rem;
		gap: 1rem;
	}

	.client__review {
		width: var(--container-width-sm);
	}
}


